body {
  margin: 0;
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*background-color: #3c4852;*/
  background-color: #f2f2f2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ag-header-viewport {
  color:darkgrey
}



.FixedControl {
  position: fixed;
  bottom: 5%;
  right: 25%;
  width: 800px;
}

.tableClass {
  margin-top:40px;
}
.Home {
    width: 80%;
    margin: auto;
    padding: 10px;
    margin-top:30px;
}

.center {
}


.button {
    margin-top:50px;
}
.center-components {
}

.top_margin {
}

.FormGrp {
    width: 20%;
}
.StatsCardContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.StatsCard {
  width:16.66%;
  margin: 10px;
}

.statContainer {
    display: flex;
    flex-wrap: wrap;
  }

  .statContainer-icon {
    order: 1;
    width: 30%;
  }

  .statContainer-displayContainer {
    order: 2;
    display: flex;
    flex-wrap: wrap;
    justify-content: right;
    width: 70%;
  }
  
  .statContainer-name {
    order: 1;
    font-size: 14px; 
    color: darkgrey;
    width: 100%;
  }
  
  .statContainer-stat {
    order: 2;
    font-style: italic;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    width: 100%;
  }
  
  .statContainer-value {
    font-size: 30px;
  }

  .statContainer-unit {
    margin-top: 10%;
    margin-left:10%;
  }
.GraphCardContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.GraphCard {
    width:20%;
    margin: 10px;
  }
  
  .graphContainer {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }
    
    .graphContainer-name {
      order: 1;
      font-size: 14px; 
      width: 100%;
      color: darkgrey;
    }
    
    .graphContainer-graph {
      order: 2;
      margin-top: 30px;
      font-style: italic;
    }
    
  
.Controls {
    display: flex;
    flex-wrap: nowrap;
    justify-content: left;
}

.StartStopControl {
    order: 1;
    font-size: 14px; 
}

.Divider {
    margin-left: 10px;
    order:2;
}

.NodeControl {
    order:3;
}

.TxRateControl {
    order:4;
}

.MinerRateControl {
    order:5;
}
.PlusMinusControl {
    display: flex;
    flex-wrap: wrap;
    margin-left:15px;
    margin-right:15px;
    border: 2em;
    border-color: azure;
    background-color: #f2f2f2; 
}

.AddIcon {
    order: 1;
}

.RemoveIcon {
    order: 3;
}

.NameTag {
    order: 2;
    margin-left:15px;
    margin-right:15px;
    margin-top: 5px;
}
.Logs {
    width: 80%;
    margin: auto;
    padding: 10px;
    margin-top:30px;
}

.Terminal {
    background-color: black;
    color: antiquewhite;
    height:500px;
    width: 100%;
    overflow: scroll;
}

